import React from 'react';
import Grid from '@material-ui/core/Grid';

import './ProjectItem.css'

const ProjectItem = props => (
    <Grid className={`on-${props.onRight ? 'right' : 'left'}`} item spacing={2}>
            {props.onRight 
                ? <span className="center">
                    <a href={props.link} className="text-red">{props.title}</a>
                    <p className="text-large">{props.desc}</p>
                </span>
                :
                null
            }
            <div className={`ProjectItem-${props.proj}`}>
            </div>
            {!props.onRight 
                ? <span className="center">
                    <a href={props.link} className="text-red">{props.title}</a>
                    <p className="text-large">{props.desc}</p>
                </span>
                :
                null
            }
    </Grid>
);

export default ProjectItem;